  border-color: #c0c4cc;
}
.select-normal-focus[data-v-116f0ef6] {
  border-color: #409EFF;
}
.select-panel[data-v-116f0ef6] {
  width: 100%;
  min-width: 120px;
  background-color: #FFFFFF;
  position: absolute;
  margin-top: 10px;
  border: 1px solid #E4E7ED;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  border-radius: 4px;
  z-index: 999;
}
.select-panel[data-v-116f0ef6]:before, .select-panel[data-v-116f0ef6]:after {
  position: absolute;
  content: '';
  width: 10px;
  height: 10px;
  top: -5px;
  left: 20%;
  margin-left: -7px;
  z-index: -1;
  pointer-events:none;
  transform: matrix(0.707, 0.707, -0.707, 0.707, 0, 0);
  -ms-transform: matrix(0.707, 0.707, -0.707, 0.707, 0, 0);		/* IE 9 */
  -moz-transform: matrix(0.707, 0.707, -0.707, 0.707, 0, 0);	/* Firefox */
  -webkit-transform: matrix(0.707, 0.707, -0.707, 0.707, 0, 0);	/* Safari and Chrome */
  -o-transform: matrix(0.707, 0.707, -0.707, 0.707, 0, 0);
}
.select-panel[data-v-116f0ef6]:before {
  background-color: #E4E7ED;
}
.select-panel[data-v-116f0ef6]:after {
  top: -4px;
  background-color: #ffffff;
}
.select-panel ul[data-v-116f0ef6] {
  overflow: auto;
  padding: 6px 0px;
  height: 100%;
}
.fade-enter-active[data-v-116f0ef6], .fade-leave-active[data-v-116f0ef6] {
