@charset "utf-8";
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  /*-moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;*/
}

body, html {
  font-size: 14px;
  font-family: "Helvetica", "\5FAE\8F6F\96C5\9ED1", Arial, SimSun, sans-serif;
  color: #333;
  min-width: 1270px;
}

@font-face {
  font-family: 'iconfont';
  src: url('fonts/iconfont.eot?ksplim');
  src: url('fonts/iconfont.eot?ksplim#iefix') format('embedded-opentype'),
  url('fonts/iconfont.ttf?ksplim') format('truetype'),
  url('fonts/iconfont.woff?ksplim') format('woff'),
  url('fonts/iconfont.svg?ksplim#iconfont') format('svg');
  font-weight: normal;
  font-style: normal;
}

body, ul, li, dl, dt, dd, p, ol, h1, h2, h3, h4, h5, h6, form, img, table, fieldset, legend {
  margin: 0;
  padding: 0;
}

ul, li, ol {
  list-style: none;
}

img, fieldset {
  border: 0;
}

img {
  /*display: block;*/
}

a {
  text-decoration: none;
  color: #333;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 100;
}

