/***********/
.content > div {
  height: 100%;
  width: 100%;
}

.content-main {
  width: 100%;
  padding: 20px;
  height: calc(100% - 50px);
}

.content-main > div {
  vertical-align: top;
  background-color: #ffffff;
  box-shadow: 0px 1px 2px 1px #ccc;
  border-radius: 6px;
  padding: 10px;
  height: 100%;
  min-height: calc(100vh - 150px);
}

/**用户管理-角色管理-机构管理- 权限管理 公用表格样式**/
.table-bg {
  border-radius: 6px;
  background: #fff;
  box-shadow: 0px 1px 2px 1px #ccc;

}

/*.table-com:nth-child(even){
	  background-color: #f4f4f4;
}*/
.table-com {
  float: left;
  width: 96%;
  margin-left: 2%;
  background: #fff;
  border-top: 1px solid #ccc;
}

.table-com li {
  height: 33px;
  line-height: 33px;
  text-align: center;
  margin: 5px 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

.table-com li:first-child {
  width: 5%;
}

.table-com li:nth-of-type(2) {
  width: 4%;
}

.table-com li.tabel-operation .operation_delete {
  color: #fff;
  background-color: #ff4949;
}

.table-com li.tabel-operation .operation_edit {

}

.table-com li input {

  vertical-align: sub;
}

.table-h {
  border-bottom: 1px solid #ccc;
  border-top: 0;
}

.table-h li:hover {
  background-color: rgba(222, 233, 246, 0.8);
  border-radius: 4px;
}

.table-body .table-com:hover {
  background: #f7f7f7;
  cursor: pointer;
}

