
.window-windowWrap[data-v-5dc6574f] {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  z-index: 999;
}
.window-maskLayer[data-v-5dc6574f] {
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0.8;
  -moz-opacity: 0.8;
  filter: alpha(opacity=80);
  position: absolute;
  width: 100%;
  height: 100%;
}
.window-maskLayerContent[data-v-5dc6574f] {
  position: absolute;
  height: 100%;
  width: 100%;
  display: table;
}
.window-container[data-v-5dc6574f] {
